import React from "react";
import "./jumbotron.css";

const Jumbotron = (props) => (
     <div className="size">
         <p></p>
            <h1 className="header">Trivia Time!</h1>
                {/* <button style={button} onClick={console.log(props)}>Test Click!</button> */}
                {props.user === null ? "" : <div className="userHeader"><p>Gamer:</p><h3>{props.user}</h3></div>}
     </div>
);

export default Jumbotron;


